<template>
    <div v-if="showDialog">
        <div class="dialog">
            <div class="dialog-mask"></div>
            <!---->
            <div class="dialog-main device-test-1" v-if="curStep===0">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog"></span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-1-content">
                        <img src="@/assets/img/networkImg.png" alt="">
                        <div class="title"></div>
                        <div class="des"></div>
                        <div class="des"></div>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="closeDialog"></button>
                    <button class="btn btn-orange" @click="curStep++"></button>
                </div>
            </div>
            <!---->
            <div class="dialog-main device-test-2" v-if="curStep===1">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog"></span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-2-content">
                        <div class="top">
                            <step :current-step="curStep"></step>
                            <el-progress type="circle"
                                         :stroke-width="15"
                                         color="#FF7B00"
                                         style="margin-top:4rem"
                                         :percentage="circleValue"></el-progress>
                        </div>
                        <div class="bottom">
                            <div class="title"></div>
                            <p>1.WiFi</p>
                            <p>2.</p>
                            <p>3.</p>
                        </div>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-orange" @click="testNet"></button>
                </div>
            </div>
            <!---->
            <div class="dialog-main device-test-3" v-if="curStep===2">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog"></span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-3-content">
                        <step :current-step="curStep"></step>
                        <div class="device-box">
                            <span></span>
                            <!--<input type="text"
                                   placeholder="/"
                                   class="ipt">-->
                            <el-select v-model="curHeadPhoneDeviceId"
                                       @change="setCurHeadPhone"
                                       placeholder="">
                                <el-option
                                        v-for="(item,index) in headPhones"
                                        :key="index"
                                        :label="item.label"
                                        :value="item.deviceId">
                                </el-option>
                            </el-select>
                            <a href="javascript:void(0)" @click="getMediadevices(1)"></a>
                            <!--{{curHeadPhoneDeviceId}}-->
                            <!--element.setSinkId(curHeadPhoneDeviceId)-->
                        </div>
                        <div class="audio-btn"
                             @click="playAudio"
                             style="cursor: pointer">
                            <img src="@/assets/img/playing.gif"
                                 v-show="playingAudio"
                                 class="playing playing-left"
                                 alt="">
                            <img src="@/assets/img/playing.gif"
                                 v-show="playingAudio"
                                 class="playing playing-right"
                                 alt="">
                            <!--<span v-show="playingAudio"></span>-->
                        </div>
                        <audio :src="mp3"
                               loop="loop"
                               style="display: none"
                               id="testAudio"
                               controls="controls"></audio>
                        <p></p>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="testOutput(2)"></button>
                    <button class="btn btn-orange" @click="testOutput(1)"></button>
                </div>
            </div>
            <!---->
            <div class="dialog-main device-test-3" v-if="curStep===3">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog"></span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-3-content">
                        <step :current-step="curStep"></step>

                        <div class="device-box">
                            <span></span>
                            <!--<input type="text"
                                   placeholder=""
                                   class="ipt">-->
                            <el-select v-model="curInputDeviceId"
                                       placeholder="">
                                <el-option
                                        v-for="(item,index) in inputDeviceList"
                                        :key="index"
                                        :label="item.label"
                                        :value="item.deviceId">
                                </el-option>
                            </el-select>
                            <a href="javascript:void(0)" @click="getMediadevices(2)"></a>
                        </div>


                        <audio autoplay
                               style="display: none"
                               id="au"
                               controls="controls"></audio>


                        <div class="input-btn" style="cursor: pointer">
                            <img src="@/assets/img/playing.gif"
                                 v-show="voiceSize"
                                 class="playing playing-left"
                                 alt="">
                            <img src="@/assets/img/playing.gif"
                                 v-show="voiceSize"
                                 class="playing playing-right"
                                 alt="">
                        </div>
                        <p></p>
                        <p></p>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="testInput(2)"></button>
                    <button class="btn btn-orange" @click="testInput(1)"></button>
                </div>
            </div>
            <!---->
            <div class="dialog-main device-test-4" v-if="curStep===4">
                <div class="dialog-header">
                    <span class="close" @click="closeDialog"></span>
                </div>
                <div class="dialog-content">
                    <div class="device-test-4-content">
                        <step :current-step="curStep"></step>
                        <ul class="test-result-list">
                            <ul>
                                <li>
                                    <span class="tit"></span>
                                    <span class="res"
                                          :class="{'bad':!devicesStatusObj.net.level===3}">{{getNetStatus()}}</span>
                                </li>
                                <li>
                                    <span class="tit"></span>
                                    <span class="res"
                                          :class="{'bad':!devicesStatusObj.output.passed}">{{devicesStatusObj.output.passed?'':''}}</span>
                                </li>
                                <li>
                                    <span class="tit"></span>
                                    <span class="res" :class="{'bad':!devicesStatusObj.input.passed}">{{devicesStatusObj.input.passed?'':''}}</span>
                                </li>
                            </ul>
                        </ul>
                    </div>
                </div>
                <div class="dialog-bottom">
                    <button class="btn btn-white" @click="reTest"></button>
                    <button class="btn btn-orange" @click="closeDialog"></button>
                </div>
            </div>
            <!--<button @click="curStep++" style="position: fixed;top:0;left:0;z-index:2000">next</button>-->
        </div>
    </div>
</template>

<script>
    import Cookie from 'cookie';
    import {mapActions,mapGetters} from 'vuex'
    import step from '@/components/devicesTest/step.vue'
    import mp3 from '@/assets/audios/music.mp3';
    import utils from '@/assets/js/helper';
    let scriptProcessor = null,
        audioContext = null,
        navMediaDevices = navigator.mediaDevices
    export default {
        props:{
            show:{
                type:Boolean
            }
        },
        data: () => ({
            inputAudio:null,
            curHeadPhoneDeviceId:'',
            curInputDeviceId:'',
            headPhones:[],///
            inputDeviceList:[],//
            audio:null,
            playingAudio:false,
            mp3:mp3,
            curStep:0,//0 1. 2./ 3. 4.
            showDialog:false,
            submitAble:false,
            circleValue: 0,
            devicesSteps: [
                {name: '',step:1},
                {name: '/',step:2},
                {name: '',step:3},
                {name: '',step:4}
            ],
            devicesStatusObj:{
                net:{
                    passed:false,
                    level:1//1. 2. 3.
                },
                output:{
                    passed:false
                },
                input:{
                    passed:false
                }
            },
            hasConnectVoiceApi:false,
            voiceSize:0,
            downlink:0,
            audiooutputDevice:'',//
            audioinputDevice:''//
        }),
        //
        destroyed(){
            this.disConnectVoiceProcess()
        },
        //
        activated(){
            if(this.curStep===1){
                this.circleValue = 0
            }
            this.connectVoiceProcess()
        },
        //
        deactivated(){
            this.disConnectVoiceProcess()
        },
        methods: {
            ...mapActions('course', ['getBookListWebActions']),
            getNetStatus(){
                let result = ''
                let netLevel = this.devicesStatusObj.net.level
                switch(netLevel){
                    case 1:
                        result = ''
                        break
                    case 2:
                        result = ''
                        break
                    case 3:
                        result = ''
                        break
                    default:
                        result = '-'
                }
                return result
            },
            ///
            setCurHeadPhone(){
                this.audio.setSinkId(this.curHeadPhoneDeviceId)
            },
            //type1.type2.
            getMediadevices(type,bol){
                if(submitAble){
                    return
                }
                let submitAble = true
                let refresh = bol===undefined?true:bol
                utils.mediadevice.getDevices().then((data)=>{
                    if(type===1){
                        this.curHeadPhoneDeviceId = ''
                        this.headPhones = data.headphones
                        window.console.log(data.headphones,'')
                    }else{
                        this.refreshInputDevice(refresh)
                        this.curInputDeviceId = ''
                        this.inputDeviceList = data.audio
                        window.console.log(data.audio,'')
                    }
                    submitAble = false
                },()=>{
                    this.$bus.$emit('showWebrtcWarn',true)
                    this.closeDialog()
                    submitAble = false
                })
            },
            //
            bindAudioEvent(){
                if(!this.audio){
                    this.audio = document.getElementById('testAudio')
                    this.audio.addEventListener('playing',()=>{
                        this.playingAudio = true
                    })
                    this.audio.addEventListener('pause',()=>{
                        this.playingAudio = false
                    })
                }
            },
            //
            playAudio(){
//                this.bindAudioEvent()
                if(this.audio!==null){
                    if(this.audio.paused){
                        this.audio.play();
                    }else{
                        this.audio.pause();
                    }
                }
            },
            closeDialog(){
                this.showDialog = false
                this.curStep = 0
                this.$emit('closeCb')
            },
            //
            connectVoiceProcess(){
                if(this.curStep === 3){
                    if(scriptProcessor && audioContext && !this.hasConnectVoiceApi){
                        this.inputAudio && this.inputAudio.play()
                        scriptProcessor.connect(audioContext.destination);
                        this.hasConnectVoiceApi = true
                    }
                }
            },
            //
            disConnectVoiceProcess(){
                if(this.hasConnectVoiceApi){
                    if(scriptProcessor && audioContext){
                        this.inputAudio && this.inputAudio.pause()
                        scriptProcessor.disconnect(audioContext.destination);
                        this.hasConnectVoiceApi = false
                    }
                }
            },
            //
            refreshInputDevice(bol){
                //
                if(bol){
                    this.disConnectVoiceProcess()
                    if(scriptProcessor && scriptProcessor.onaudioprocess){
                        scriptProcessor.onaudioprocess = null
                        scriptProcessor = null
                    }
                    this.addVoiceListener()
                }
            },
            //
            testNet(){
                if(this.submitAble){
                    return
                }
                this.submitAble = true
                let timeCount = 0
                let startDate = new Date().getTime()
                if(navigator.onLine){
                    //80%20100%
                    let interval = setInterval(()=>{
                        this.circleValue +=2
                        if(this.circleValue>=80){
                            this.circleValue=80
                            clearInterval(interval)
                        }
                    },50)
                    let params = {
                        page_num:1,
                        page_size:5
                    }
                    //  
                    this.getBookListWebActions(params).then((res)=>{
                        let curDate = new Date().getTime()
                        timeCount = Number(((curDate-startDate)/1000).toFixed(2))
                        //3 3~5 5
                        this.devicesStatusObj.net.passed = true
                        if(timeCount<=3){
                            this.devicesStatusObj.net.level = 1
                        }else if(timeCount>3 && timeCount<=5){
                            this.devicesStatusObj.net.level = 2
                        }else{
                            this.devicesStatusObj.net.level = 3
                        }
                        this.circleValue=100
                        clearInterval(interval)
                        let timer = setTimeout(()=>{
                            this.curStep ++
                            this.submitAble = false
                            clearTimeout(timer)
                        },500)
                    },()=>{
                        this.submitAble = false
                        this.circleValue=0
                        clearInterval(interval)
                        this.$message.error('')
                    })

                    /*setTimeout(()=>{
                        let curDate = new Date().getTime()
                        timeCount =parseInt((curDate-startDate)/1000)
                        this.submitAble = false
                        this.circleValue=100
                        clearInterval(interval)
                        alert(timeCount)
                    },4000)*/
                }else{
                    this.submitAble = false
                    this.$message.error('')
                    this.devicesStatusObj.net.passed = false
//                    this.currentStep = 2
                }
            },
            // type 1: 2:
            testOutput(type){
                if(type===1){
                    this.devicesStatusObj.output.passed = true
                }else{
                    this.devicesStatusObj.output.passed = false
                }
                if(this.audio){
                    this.audio.pause();
                }
                this.curStep ++
            },
            // type 1: 2:
            testInput(type){
                if(type===1){
                    this.devicesStatusObj.input.passed = true
                }else{
                    this.devicesStatusObj.input.passed = false
                }
                this.curStep ++
            },
            reTest(){
                this.curStep = 1
            },
            addVoiceListener(){
                let _this = this
                if (navMediaDevices && navMediaDevices.getUserMedia) {
                    //  media 
                    let con = {
                        audio: true
                    }
                    navMediaDevices.getUserMedia(con).then((stream) => {
                        if(!this.inputAudio){
                            this.inputAudio = document.getElementById('au')
                            this.inputAudio.srcObject = stream
                        }
                        audioContext = new AudioContext();
                        // 
                        let mediaStreamSource = audioContext.createMediaStreamSource(stream);
                        // 4096
                        scriptProcessor = audioContext.createScriptProcessor(4096,1,1);
                        // 
                        mediaStreamSource.connect(scriptProcessor);
                        //  Chrome  bug
                        scriptProcessor.connect(audioContext.destination);
                        // 
                        scriptProcessor.onaudioprocess = function(e) {
                            window.console.log(88888)
                            // PCM32
                            let buffer = e.inputBuffer.getChannelData(0);
                            // 
                            let maxVal = Math.max.apply(Math, buffer);
                            let voiceSize = Math.round(maxVal * 100);
                            _this.voiceSize = Number(voiceSize)
                        };
                        _this.hasConnectVoiceApi = true
                    }).catch((error) => {
                        window.console.log('' + error);
                    });
                } else {
                    this.$bus.$emit('showWebrtcWarn',true)
                    window.console.log('');
                }
            },
//            cookie
            deviceTestWriteToCookie(){
                if(this.accoutInfo && this.accoutInfo.phone){
                    let phone = this.accoutInfo.phone
                    let userKey = 'deviceTest' + phone
                    let t = 24 * 60 * 60 * 30 * 1000
                    let expires = new Date().getTime() + t
                    utils.cookieUtils.setItem(userKey,'true',expires)
//                    document.cookie = Cookie.serialize(userKey, 'true', {path: "/",expires});
                }
            }
        },
        mounted() {

        },
        computed: {
            ...mapGetters('account',{
                accoutInfo: 'info'
            })
        },
        components: {
            step
        },
        watch:{
            curStep:{
                handler(nv){
                    switch(nv){
                        case 0:
                            this.circleValue = 0
                            this.disConnectVoiceProcess()
                            break
                        case 1:
                            this.circleValue = 0
                            this.disConnectVoiceProcess()
                            break
                        case 2:
                            this.$nextTick(()=>{
                                this.bindAudioEvent()
                            })
                            this.disConnectVoiceProcess()
                            this.getMediadevices(1)
                            break
                        case 3:
                            //
                            if(scriptProcessor){
                                this.connectVoiceProcess()
                            }else{
                                this.addVoiceListener()
                            }
                            this.getMediadevices(2,false)
                            break
                        case 4:
                            this.disConnectVoiceProcess()
                            this.deviceTestWriteToCookie()
                            break
                        default:
                            this.disConnectVoiceProcess()
                    }
                },
                immediate:true
            },
            show:{
                handler(nv){
                    if(nv!==undefined){
                        this.showDialog = nv
                        if(!nv){
                            this.disConnectVoiceProcess()
//                            alert(nv)
                        }
                    }
                },
                immediate:true
            }
        },
        created() {
        }
    }
</script>

<style lang="postcss" scoped>

</style>
